











import { Component, Vue } from 'vue-property-decorator';

@Component
export default class About extends Vue {
  public delay: number = 5;
  public ttl: number = 0;
  public hasSubscription: boolean = false;

  private subs: any = undefined;

  public sendNotification(): void {
    fetch('https://simardo-hello-world-2.azurewebsites.net/sendnotification', {
      method: 'post',
      headers: {
        'Content-type': 'application/json',
      },
      body: JSON.stringify({
        subscription: this.subs,
        delay: this.delay,
        ttl: this.ttl,
      }),
    });
  }

  protected mounted(): void {
    navigator.serviceWorker.register('about-worker.js');
    navigator.serviceWorker.ready
      .then((registration) => {
        return registration.pushManager
          .getSubscription()
          .then(async (subscription) => {
            if (subscription) {
              return subscription;
            }

            const response: Response = await fetch('https://simardo-hello-world-2.azurewebsites.net/vapidpublickey');
            const vapidPublicKey = await response.text();
            const convertedVapidKey = this.urlBase64ToUint8Array(
              vapidPublicKey,
            );

            return registration.pushManager.subscribe({
              userVisibleOnly: true,
              applicationServerKey: convertedVapidKey,
            });
          });
      })
      .then((subscription) => {
        // console.log('here', subscription);
        this.subs = subscription;
        this.hasSubscription = true;

        fetch('https://simardo-hello-world-2.azurewebsites.net/register', {
          method: 'post',
          headers: {
            'Content-type': 'application/json',
          },
          body: JSON.stringify({
            subscription,
          }),
        });
      });
  }

  private urlBase64ToUint8Array(base64String: string) {
    const padding = '='.repeat((4 - (base64String.length % 4)) % 4);
    const base64 = (base64String + padding)
      .replace(/\-/g, '+')
      .replace(/_/g, '/');

    const rawData = window.atob(base64);
    const outputArray = new Uint8Array(rawData.length);

    for (let i = 0; i < rawData.length; ++i) {
      outputArray[i] = rawData.charCodeAt(i);
    }
    return outputArray;
  }
}
